.dashboard {
    @include box(500px, 600px, 10px solid #95192D, #DC0A2D);
    @include flex(row nowrap, space-between, center);
    overflow: hidden;
}

.dashboard__list {
    @include box(200px, 600px);
    border-right: 5px solid #95192D;
    list-style: none;
    padding: 0;
    margin: 0;
    overflow: auto;
}

.dashboard__list::-webkit-scrollbar { 
    display: none; 
}

.poke__button {
    @include box(100%, auto, none, #DC0A2D);
    border-bottom: 4px solid #95192D;
    padding: 5px;
}

.poke__button::Active {
    background: #95192D;
}


